@mixin marginLeft($width,$margin) {
	@media screen and (max-width: $width) {
		margin-left: $margin;
	}
}

@mixin marginInput($width,$marginTop,$marginBottom) {
	@media screen and (max-width: $width) {
		margin: $marginTop auto $marginBottom;
	}
}

$placeholderColor: rgba(0, 0, 0, 0.3);
$placeholderColorError: rgba(255, 0, 0, 0.3);
$shad: 0 1px 4px rgba(0, 0, 0, .3),
-23px 0 20px -23px rgba(0, 0, 0, .6),
23px 0 20px -23px rgba(0, 0, 0, .6),
inset 0 0 40px rgba(0, 0, 0, .1);

@mixin bRadius($br) {
	border-radius: $br;
	outline: none;
}

.login {
	text-align: center;

	.loginEmail, .loginPass, .loginName {
		position: relative;
		text-align: left;

		.inputField {
			margin: 2% auto 10%;
			width: 80%;
			max-height: 2.8rem;
			display: block;
			padding: 2.9% 2.9% 2.9% 7%;
			font-weight: bold;
			border: 1px solid lawngreen;
			@include bRadius(0.5rem);
			@include marginInput(750px, 5%, 15%);
			@include marginInput(650px, 10%, 20%);
			@include marginInput(500px, 12%, 25%);

			&:focus {
				border: 1px solid lightblue;
			}
		}

		@media screen and (max-width: 450px) {
			.inputField {
				width: 85%;
				margin: 0.7rem auto 0.7rem;
			}
		}

		input[type="password"]::placeholder {
			color: $placeholderColor;
		}

		input[type="password"].errorInputField::placeholder {
			color: $placeholderColorError;
		}

		input[type="email"]::placeholder {
			color: $placeholderColor;
		}

		input[type="email"].errorInputField::placeholder {
			color: $placeholderColorError;
		}

		input[type="text"]::placeholder {
			color: $placeholderColor;
		}

		input[type="text"].errorInputField::placeholder {
			color: $placeholderColorError;
		}

		.inputLabel {
			display: inline-block;
			padding-left: 5%;
			font-size: 0.8rem;

		}

		.inputLabel.errorInputLabel {
			color: red;
			display: inline-block;
		}

		.inputField.errorInputField {
			border: 1px solid red;
		}

		.inputError {
			position: absolute;
			text-align: center;
			background-color: transparent;
			font-weight: normal;
			font-size: 0.8rem;
			width: 70%;
			color: red;
			padding: 1px 2%;
			bottom: 50%;
			left: 13%;
		}
	}

	.loginCheck {
		display: flex;
		align-items: center;
		margin: 0 auto;
		flex-wrap: wrap;
		max-width: 88%;
		justify-content: space-between;


		.inputField[type=checkbox] {
			zoom: 1.7;
			display: inline-block;
			cursor: pointer;
			padding: 0.5rem;
			margin-right: 0.3rem;
		}

		.check {
			display: flex;
			align-items: center;
			position: relative;

			.checkPass {
				font-size: 0.7rem;
				padding-bottom: 2px;
			}

			.checkTitle {
				width: 10rem;
				text-align: left;
				background-color: white;
				padding: 1rem;
				@include bRadius(0.5rem);
				color: rgba(0, 0, 0, 0.4);
				position: absolute;
				top: 120%;
				opacity: 0;
			}
		}

		.checkPass:hover ~ .checkTitle {
			opacity: 100;
		}

	}

	.loginRegistrBtns {
		text-align: right;
		max-width: 94%;

		.loginRegistrBtn {
			color: white;
			cursor: pointer;
			@include bRadius(0.5rem);
			border: 2px solid transparent;

			padding: 3% 10%;
			align-self: end;
			margin-bottom: 2rem;
			font-weight: bold;
			background: radial-gradient(circle farthest-corner at bottom right, rgb(255, 180, 0), rgb(170, 120, 0));

			&:hover {
				background: radial-gradient(circle farthest-corner at bottom right, rgb(170, 120, 0), rgb(255, 180, 0));
			}

			&:active {
				border: 2px solid gray;


			}
		}

		@media screen and (max-width: 450px) {

			.loginRegistrBtn {
				padding: 2% 5%;
				margin-bottom: 1rem;
				margin-top: 0.5rem;
			}
		}
	}
}

.loginFormBtn {
	text-align: right;
	max-width: 95%;
	margin-top: 12%;

	.loginBtn {
		cursor: pointer;
		@include bRadius(0.5rem);
		padding: 3% 10%;
		border: 2px solid transparent;
		color: white;
		font-weight: bold;
		background: radial-gradient(circle farthest-corner at bottom right, rgb(79, 182, 226), rgb(0, 76, 108));

		&:hover {
			background: radial-gradient(circle farthest-corner at bottom right, rgb(0, 76, 108), rgb(79, 182, 226));
		}

		&:active {
			border: 2px solid gray;
		}
	}
}

@media screen and (max-width: 450px) {
	.loginFormBtn {
		margin-top: 5%;

		.loginBtn {
			padding: 2% 5%;
		}
	}

}

















